import { createSlice } from "@reduxjs/toolkit";
import { diaries } from "./stub";
const initialState = {
    diaries: diaries,
};
export const diarySlice = createSlice({
    initialState,
    name: "diary",
    reducers: {
        remove: (state, { payload: id }) => {
            state.diaries = state.diaries.filter((diary) => diary.id !== id);
        },
        add: (state, { payload }) => {
            state.diaries.push({
                ...payload,
                id: (Math.random() * 1000 + 10).toString(),
            });
        },
    },
});
export const diaryActions = diarySlice.actions;
export const diarySelector = (state) => state.diary;
